.cards {
  display: flex;
}
.cardImage {
  margin: 15px 0 5px 39px;
  height: 116px;
  width: 143px;
}
.itemText {
  margin: 1rem 0 5px rem;
}
footer {
  background-color: #2874f0;
  background-color: var(--blue);
  color: white;
  height: 7vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
/* --------------------Contact Css  style Start 
                        ---------------------*/
.contact-section {
  margin: 5rem 0;
}
.contact-left-side .main-hero-para {
  margin: 2rem 0;
}
.contact-left-side img {
  min-width: 80%;
  max-height: 30rem;
  height: auto;
}
.contact-right-side form .contact-input-field {
  margin-bottom: 3rem;
}
.contact-right-side form .form-control {
  padding-left: 1rem;
  height: 5rem;

  border: 2px solid rgb(151, 142, 142);
  border-radius: 1rem;
  font-size: 1.6rem;
}
.contact-right-side form .form-check-input {
  height: 2rem;
  width: 2rem;
}
.form-check-box {
  margin: 3rem 0;
}
.contact-right-side form .main-hero-para {
  margin: 0;
  padding: 0 2rem;
  width: 75%;
  color: var(--main-text-color);
  font-weight: lighter;
}
/* --------------------Contact Css  style End 
                                                ---------------------*/

@media only screen and (max-width:998){
   
   header{
    width: 415vh;
   }
    .mainImage{
        margin: 20px 15vh 0px auto;
        width: 80vw;
        overflow: hidden;
        margin: 12px 0px 0px 63vh;
        
    }
    nav{
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-right: 85vw;
    
    }
    .searchBar{
        
        margin: 10px 0px;
    }
    .searchBar input{
        
        height: 5vh;
        width: 15vw;
        border-radius: 5px;
        padding: 0 12px;
    }
}

